import { Component, OnInit, ViewEncapsulation, AfterViewInit, ElementRef, ViewChild, HostBinding} from '@angular/core';
import { Helpers } from '../../../helpers';
import { ScriptLoaderService } from '../../../_services/script-loader.service';
import { FLY_IN_OUT_ANIMATION }   from '../../../animations/fly-in-out.animation';
import { ROUND_ANTICLOCK_ANIMATION }   from '../../../animations/round-anticlock.animation';
import {HttpClientModule} from '@angular/common/http';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import { MatInputModule, MatNativeDateModule, MatSelectModule, MatSort, PageEvent} from '@angular/material';
import {CdkTableModule} from '@angular/cdk/table';
import {DataSource} from '@angular/cdk/collections';
import {BehaviorSubject} from 'rxjs/BehaviorSubject';
import {Observable} from 'rxjs/Observable';
import {FormControl, Validators, FormGroup} from '@angular/forms';
import 'rxjs/add/operator/startWith';
import 'rxjs/add/observable/merge';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/debounceTime';
import 'rxjs/add/operator/distinctUntilChanged';
import 'rxjs/add/observable/fromEvent';
import {ListComponent, SafeHtmlPipe} from '../list/list.component';
import { AmChartsService, AmChart } from "@amcharts/amcharts3-angular";
//import { SmartSearchComponent } from '../../../layouts/smart-search/smart-search.component';
 
@Component({
    selector: ".m-grid__item.m-grid__item--fluid.m-wrapper",
    templateUrl: "./report-soe-dash.component.html", 
    encapsulation: ViewEncapsulation.None,
	animations: [ 
    FLY_IN_OUT_ANIMATION, ROUND_ANTICLOCK_ANIMATION
  ],
})
export class SOEDashboardComponent implements OnInit,AfterViewInit {
	 @ViewChild(MatSort) sort: MatSort; 
	
	branchCount = 0;
	listHide: boolean = true;
	animateList: string = 'in';
	SOEDashForm: FormGroup; 
	sortField: string ="";
	private chart: AmChart;
	sortDirection: string ="";
	franchiseeCount = 0;
	populateData1 = [];
	configSalesopprName = sessionStorage.getItem('configSalesopprName');
	  
	opprCloseSummaryHeader = [];
	opprCloseSummaryData = [];
	opprSummaryData = [];
	opprSummaryHeader = [];
	  
	bootstrapFromValues = [];
	franchiseeId=[];
	branchId=[];
	exeId=[];
	modelId=[];
	teamId=[];
	soeDash = new SOEDash();

    constructor(private _script: ScriptLoaderService, 	private http: HttpClient, private AmCharts: AmChartsService) {
    	
    		  const headers = new HttpHeaders({'Content-Type':'application/json'});
		   this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/sales/report-soedash',
								 JSON.stringify({requestType:'count'}),
								{ headers, withCredentials: true }).subscribe(data =>{
																	this.franchiseeCount = parseInt(data['franchiseeCount']);
			   														this.branchCount = parseInt(data['branchCount']);
																	}); 
		
    		
    }
	   
    ngOnInit() {
		this.listHide = true;
    	this.animateList='out';
		this.populateDropDownData();
		
		this.SOEDashForm = new FormGroup({	
			'startDate' : new FormControl(''),
			'endDate' : new FormControl(''),
			'franchiseeIds' : new FormControl(''), 
			'branchIds' : new FormControl(''), 
			'exeIds' : new FormControl(''), 
			'modelIds' : new FormControl(''),
			'teamIds' : new FormControl(''),
		});
		 
		  
		 
		 
		 
				} 
				
	get startDate(){ return this.SOEDashForm.get('startDate')};
	get endDate(){ return this.SOEDashForm.get('endDate')};
	get franchiseeIds(){ return this.SOEDashForm.get('franchiseeIds')};
	get branchIds(){ return this.SOEDashForm.get('branchIds')};
	get exeIds(){ return this.SOEDashForm.get('exeIds')};
	get modelIds(){ return this.SOEDashForm.get('modelIds')};
	get teamIds(){ return this.SOEDashForm.get('teamIds')};		
				
	  toggleAnimation(){ 
	  }
	  populateDropDownData(){
		  	
		  const headers = new HttpHeaders({'Content-Type':'application/json'});
		   this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/branch',
								 JSON.stringify({}),
								{ headers, withCredentials: true }).subscribe(data =>{
																	this.branchId = data['populateBranch']
																	}); 
		  this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/team',
								 JSON.stringify({}),
								{ headers, withCredentials: true }).subscribe(data =>{
																	this.teamId = data['populateTeam']
																	}); 
		  this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/franchisee',
								 JSON.stringify({ }),
								{ headers, withCredentials: true }).subscribe(data =>{
																	this.franchiseeId = data['populateFranchisee']
																	}); 
		  this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/model',
								 JSON.stringify({}),
								{ headers, withCredentials: true }).subscribe(data =>{
																	this.modelId = data['populateModel']
																	}); 
		  this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/teamexecutives',
								 JSON.stringify({ franchiseeIds :''}),
								{ headers, withCredentials: true }).subscribe(data =>{
																	this.exeId = data['populateExecutive']
																	}); 
		  
	 }
	  populateData(){
			  	 
	 } 
    ngAfterViewInit() {
		 $.getScript('assets/app/js/bootstrap-material-datetimepicker.js',function(){});
    }
	  
	  onSubmit() {
		  this.listHide = false;
		  this.bootstrapFromValues = $('#m_form_1').serializeArray();
			for(var i=0;i<this.bootstrapFromValues.length;i++){
				this.soeDash[this.bootstrapFromValues[i].name]=this.bootstrapFromValues[i].value;
			}
		  
		   const headers = new HttpHeaders({'Content-Type':'application/json'});
		   this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/sales/report-soedash',
								 JSON.stringify(this.soeDash),
								{ headers, withCredentials: true }).subscribe(data =>{
			   					this.opprSummaryHeader = data['OpprSummaryHeader'];
			   					this.opprSummaryData = data['opprSummary'];
			   					this.opprCloseSummaryHeader = data['opprCloseSummaryHeader'];
			   					this.opprCloseSummaryData = data['opprCloseSummary'];
			   					console.log(this.opprCloseSummaryData);
								this.chart =  this.AmCharts.makeChart( "chartdiv1", {
						         "type": "pie",
						         "theme": "light",
						         "legend":{
						         "position":"bottom",
//						         "marginRight":100,
						         "autoMargins":false
						         },
						         "dataProvider": data['data'],
						         "titleField": "title",
						         "marginRight": 160,
						         "marginLeft": 15,
						         "labelPosition": "right",
						         "funnelAlpha": 0.9,
						         "valueField": "value",
						         "startX": 0,
						         "neckWidth": "40%",
						         "startAlpha": 0,
						         "outlineThickness": 1,
						         "neckHeight": "30%",
						         "balloonText": "[[title]]:<b>[[value]]</b>",
						         "export": {
						           "enabled": true
						         }
						  } );
																	
																	}); 
		   
	  }
//	   listClickListener(listParam: any[]){
//		 
//	}

      franchiseeOnChange(event){
		  const headers = new HttpHeaders({'Content-Type':'application/json'});
		   this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/branch',
								 JSON.stringify({ franchiseeIds :event.value , requestType:'filter'}),
								{ headers, withCredentials: true }).subscribe(data =>{
																	this.branchId = data['populateCampaignType']
																	}); 
		
	}
	// sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/campaigntype'
	branchOnChange(event){
		  const headers = new HttpHeaders({'Content-Type':'application/json'});
		   this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/team',
								 JSON.stringify({ branchIds :event.value}),
								{ headers, withCredentials: true }).subscribe(data =>{
																	this.teamId = data['populateTeam']
																	}); 
		
	}

	teamOnChange(event){
		  const headers = new HttpHeaders({'Content-Type':'application/json'});
		   this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/teamexecutives',
								 JSON.stringify({ teamIds :event.value}),
								{ headers, withCredentials: true }).subscribe(data =>{
																	this.exeId = data['populateExecutive']
																	}); 
		
	}

ngOnDestroy() {
        if (this.chart) {
          this.AmCharts.destroyChart(this.chart);
        }
      }
      
}

export class SOEDash {
startDate: string;
endDate: string;
franchiseeIds: string[];
branchIds: string[];
exeIds: string[];
modelIds: string[];
teamIds: string[];
}




